
.block {
    position: relative;
    display: flex;
    border: 2px solid burlywood;
    margin-top: 50px;
    margin-left: 150px;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: rgb(218, 202, 172);
    box-shadow: inset 0 0 rgba(168, 157, 136, 0.479);
    transition: box-shadow 0.3s, ;
}
.text {
    display: flex;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    transition: top 0.4s, left 0.4s;
}
.spin {
    position: absolute;
    border-radius: 50%;
    height: 100%;
    width: 100%;
    transition: box-shadow 0.4s;
    box-shadow: 
                0 0 rgba(255, 0, 0, 0.3), 
                0 0 rgba(255, 68, 0, 0.3), 
                0 0 rgba(255, 238, 0, 0.3), 
                0 0 rgba(102, 255, 0, 0.3), 
                0 0 rgba(0, 255, 242, 0.3), 
                0 0 rgba(17, 0, 255, 0.3), 
                0 0 rgba(140, 0, 255, 0.3), 
                0 0 rgba(255, 0, 200, 0.3);
    
    
}
@keyframes circle {
0% {transform: rotate(0);}
100% {transform: rotate(360deg);}
}    

.spin:hover {
    animation: circle 2s linear infinite;
    transition: box-shadow 0.4s 0.2s; 
    box-shadow:
    -9px -9px rgba(255, 0, 0, 0.3), 
    -12px 0 rgba(255, 68, 0, 0.3), 
    -9px 9px rgba(255, 238, 0, 0.3), 
     0 12px rgba(102, 255, 0, 0.3), 
     9px 9px rgba(0, 255, 242, 0.3), 
     12px 0 rgba(17, 0, 255, 0.3), 
     9px -9px rgba(140, 0, 255, 0.3), 
     0 -12px rgba(255, 0, 200, 0.3);
}
.block:hover  {
        background-color: rgb(218, 202, 172);
        box-shadow: inset -5px 5px rgba(168, 157, 136, 0.479);
        & .text {
    top: 1px;
    left: -1px;
}
    } 
